
:root {
  --grid: 20px;
  --grid-color: #bbb;
  --field-border-width: 2px;
}

@import url('https://fonts.googleapis.com/css2?family=Neucha&display=swap');

body {
  margin: 0;
  font-family: 'Neucha', cursive;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: repeating-linear-gradient(transparent,transparent calc(var(--grid) - 1px), var(--grid-color) var(--grid) ),
              repeating-linear-gradient( 90deg, transparent,transparent calc(var(--grid) - 1px), var(--grid-color) var(--grid) );;
  min-height: 100vh;
  font-size: calc(10px + 2vmin);
}

body::before {
  content: '';
  display: block;
  position: absolute;
  right: calc(var(--grid) * 4 - var(--field-border-width));
  top: 0;
  background: #ff8100;
  height: 100vh;
  width: var(--field-border-width);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}